import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import BaseMap from './BaseMap'
import BaseStaticMap from './BaseStaticMap'
import style from './MapDot.style'

const MapDot = ({ classes, staticMap, editable, onChange, direction, ...props }) => {
  const Map = staticMap ? BaseStaticMap : BaseMap
  const mapProps = staticMap ? {} : {
    touchRotate: false,
    dragRotate: false,
    lockPan: !editable,
    onViewportChange: onChange
  }

  return (
    <Map
      {...mapProps}
      {...props}
    >
      <svg className={classes.svgDot}
        version='1.1'
        viewBox='0, 0, 10, 10'
      >
        <circle cx='5' cy='5' r='4'
          stroke='#CCCCFF'
          strokeWidth='1'
          fill='#4444FF'
        />
      </svg>
      {direction && direction >= 0 ? (
        <svg className={classes.svgArc}
          version='1.1'
          width='100'
          height='100'
          viewBox='0, 0, 100, 100'
          style={{ transform: `translateY(-50%) translateX(-50%) rotateZ(${direction}deg)` }}>
          <path d='M50,0 C60.946,0.151 72.136,3.514 80.675,10.56 L50,50 L19.325,10.56 C27.938,3.461 39,0.278 50,0 z'
            fill='#009DC1'
            fillOpacity='0.444'
            strokeWidth='0.5'
            strokeOpacity='0.6'
            stroke='#FFFFFF'
          />
        </svg>
      ) : null}
    </Map>
  )
}

MapDot.propTypes = {
  classes: PropTypes.object.isRequired,
  direction: PropTypes.number,
  editable: PropTypes.bool,
  staticMap: PropTypes.bool,
  onChange: PropTypes.func
}

export default withStyles(style)(MapDot)
