import React, { useState } from 'react'
import PropTypes from 'prop-types'
import SpeedDial from '@material-ui/lab/SpeedDial'
import SpeedDialAction from '@material-ui/lab/SpeedDialAction'
import MapLayersIcon from '@material-ui/icons/Layers'

import { withStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    zIndex: 10,
    right: theme.spacing.unit / 2,
    position: 'absolute'
  },
  speedDial: {
    // bottom: theme.spacing.unit * 10,
    // right: theme.spacing.unit * 3
  },
  speedDialFab: {
    position: 'absolute',
    right: 0,
    border: '2px solid',
    background: theme.palette.grey[400],
    color: 'white',
    borderRadius: theme.spacing.unit,
    height: 36,
    width: 36,
    fontSize: 20
  },
  directionDown: {
    // marginTop: -36,
    // paddingTop: 36
  },
  speedDialActions: {
    marginTop: 0,
    paddingTop: 36
  },
  speedDialActionsOpen: {
    // marginTop: -36,
    // paddingTop: 36
  },
  iconButton: {
    // background: theme.palette.primary.dark,
    // color: theme.palette.primary.light,
    margin: theme.spacing.unit / 2,
    padding: theme.spacing.unit / 2,
    border: '2px solid',
    borderRadius: theme.spacing.unit,
    background: theme.palette.common.white,
    fontSize: 20
  },
  active: {
    background: theme.palette.secondary.dark,
    color: 'white'
  },
  icon: {

  }
})

const StyleButtons = ({ classes, theme, controlPadding, currStyle, mapStyles, setCurrStyle }) => {
  const [open, setOpen] = useState(false)

  const noEventSetOpen = (e, state) => {
    e.preventDefault()
    e.stopPropagation()
    setOpen(state)
  }

  if (!mapStyles || !Array.isArray(mapStyles) || mapStyles.length < 2 || !mapStyles[currStyle]) return null

  const currStyleObj = mapStyles[currStyle]
  const ActiveIcon = currStyleObj.ActiveIcon

  return (
    <div className={classes.root} style={{
      // top: theme.spacing.unit + theme.mixins.toolbar.minHeight
      top: (theme.spacing.unit) + (controlPadding.top ? controlPadding.top : 0)
    }}>
      {mapStyles.length > 1 && (
        <SpeedDial
          ariaLabel='SpeedDial example'
          className={classes.speedDial}
          classes={{
            fab: classes.speedDialFab,
            actions: classes.speedDialActions,
            directionDown: classes.directionDown
          }}
          icon={<MapLayersIcon className={classes.icon} />}
          onBlur={(e) => noEventSetOpen(e, false)}
          onClick={(e) => noEventSetOpen(e, !open)}
          onClose={(e) => noEventSetOpen(e, false)}
          onFocus={(e) => noEventSetOpen(e, true)}
          onMouseEnter={(e) => noEventSetOpen(e, true)}
          onMouseLeave={(e) => noEventSetOpen(e, false)}
          open={open}
          direction='down'
        >

          {mapStyles.map(({ Icon, ActiveIcon, toolTip }, idx) => (
            <SpeedDialAction
              key={`stylebutton${idx}`}
              // Bit of a hack here. This function isn't quite ready for primetime
              icon={<Icon className={classes.activeIcon} />}
              tooltipTitle={toolTip}
              tooltipOpen
              onClick={() => {
                setCurrStyle(idx)
                setOpen(false)
              }}
            />
          ))}

        </SpeedDial>
      ) }
    </div>
  )
}

StyleButtons.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
  // controlPadding: {top, right, bottom, left}
  controlPadding: PropTypes.object,
  currStyle: PropTypes.number,
  mapStyles: PropTypes.array,
  setCurrStyle: PropTypes.func
}
StyleButtons.defaultProps = {
  controlPadding: {}
}

export default withStyles(style, { withTheme: true })(StyleButtons)

